import Vue from 'vue'
import Router from 'vue-router'

const views = [
  {
    name: 'guide',
    module: require('@/views/guide')
  },
  {
    name: 'chunk',
    module: require('@/views/chunk')
  },
  {
    name: 'compact',
    module: require('@/views/compact')
  },
  {
    name: 'concat',
    module: require('@/views/concat')
  },
  {
    name: 'difference',
    module: require('@/views/difference')
  }
].map(item => {
  return {
    path: `/${item.name}`,
    name: item.name,
    component: item.module.default
  }
})

// 将路由信息挂在到Router原型上，便于main.js里调用，这样就不需要在App.vue重新定义路由路径了
Router.prototype.paths = views.map(i => {
  return i.name
})

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      redirect: {name: 'guide'}
    }].concat(views)
})



